import React, { Component } from 'react';
import './App.css';
import okButton from './images/okButton.png';
import Page2 from './Page2';
import pa1 from './images/pa1.jpg';
import pa3 from './images/pa3.jpg';
import $ from 'jquery'

class App extends Component {
  constructor(props){
    super(props)
    this.state = {page : "goToMain" , pageindex : 0}
    this.indexPage = 0
    // this.handleApp = this.handleApp.bind(this)
  }
  // handleApp(herf){
  //   console.log("handleApp: " + herf)
  //   this.setState((prevState)=>{
  //     return {page : herf}
  //   })
  // }

  MainButtonClick = (e) => {
    // e.preventDefault()
    // this.setState((prevState , props) => {
    //    var pageindex = (prevState.pageindex + 1) % 3
    //    return {
    //     pageindex : pageindex
    //    }
    //   } 
    // )
    
    this.indexPage = (this.indexPage + 1) % 3
    console.log("this.indexPage" , this.indexPage)
    
    switch (this.indexPage) {
      case 0:
      console.log(0)
        $(".page2bg").css("z-index", "-1");
        $(".bg").css("background-image" ,`url(${pa1})`)
        break
      case 1:
      console.log(1)
        $(".page2bg").css("z-index", "0");
        break
      case 2:
      console.log(2)
        $(".bg").css("background-image" ,`url(${pa3})`)
        $(".page2bg").css("z-index", "-1");
        break
      default:
        break
    }
    
  }
  render() {
    // return (
    //   <Main page={this.state.page} pageHandle={this.handleApp}/>
    // );
    
    var bgStyle = {
      backgroundImage: `url(${pa1})`,
      backgroundSize: "cover",
      };
    
    

    // switch(this.state.pageindex) {
    //   case 0:
          // var bgStyle = {
          //   backgroundImage: `url(${pa1})`,
          //   backgroundSize: "cover",
            
          //   };
          // mainContent = <div className="bg" style={bgStyle}></div>
    //       break;
    //   case 1:
    //       mainContent = <div className="c1">{this.page2}</div>
    //       break;
    //   case 2:
    //       mainContent = 
    //                     <div className="bg" style={{backgroundImage:`url(${pa3})` ,
    //                         backgroundSize: "cover"
    //                     }}></div>
                       
    //   default:
    //       break;
    // }
  return ( <div className="c1">
          <div className="bg" style={bgStyle}></div><Page2 />
          <button className="startButton">
          <img className= "startButtonImage" src={okButton} alt="" onClick={this.MainButtonClick} />
          </button>
        </div>
      )

  }
}

export default App;



